<template>
  <div>
    <v-data-table
      :headers="inputProps.headers"
      :items="showData"
      :items-per-page="5"
      :loading="bLoading"
      :search="search"
      loading-text= "Loading... Please wait"
      class="elevation-24"
    >
      <template v-slot:top>
        <v-row class = "d-flex flex-row justify-space-between flex-wrap align-center">
          <v-col cols="12" sm="2" md="2" class="pl-12"> 
            <h3> {{ inputProps.formName }} </h3>
          </v-col>
          <v-col cols="12" sm="2" md="2" v-if="inputProps.isServer"> 
            <v-autocomplete
              v-model="selectedServers"
              :items="inputProps.servers"
              outlined
              small-chips
              label="Server"
              multiple
              auto-select-first
              deletable-chips
              clearable>
            </v-autocomplete>
          </v-col>
          <v-col cols="12" sm="4" md="4" v-if="inputProps.isParent"> 
            <v-autocomplete
              v-model="selectedParents"
              :items="inputProps.parents"
              outlined
              small-chips
              label="Parent"
              multiple
              auto-select-first
              deletable-chips
              clearable>
            </v-autocomplete>
          </v-col>
          <v-col cols="12" sm="4" md="4" class="pr-12">
            <v-text-field v-model="search" class = "mb-3" label="search" append-icon="mdi-text-search" single-line hide-details auto></v-text-field>
          </v-col>
        </v-row>
        <v-divider class = "grey lighten-2" horizontal></v-divider>
      </template>
      <template v-slot:item.PA_VALUE="props">
        <div>
        <v-edit-dialog
          class="disable-events"
          :return-value.sync="props.item.PA_VALUE"
          large
          @save="saveEdit(props.item)"
          @cancel="cancelEdit"
          @open="openEdit"
          @close="closeEdit"
        >
        <div>{{ props.item.PA_VALUE }}</div>
          <template v-slot:input>
            <div class="mt-4">
              <v-badge
                :value="isDisabled"
                :content="isDisabledText"
                bordered
                color="warning"
              >
              Update Parameter
              </v-badge>
            </div>

            <v-text-field
              v-model="props.item.PA_VALUE"
              label="Edit"
              :disabled = "isDisabled"
              single-line
              counter
              autofocus
            ></v-text-field>
          </template>
        </v-edit-dialog>
        </div>
      </template>
      <template v-slot:no-data>
        No data, or select a profile
      </template>
    </v-data-table>
    <v-snackbar
      v-model="snack"
      :timeout="3000"
      :color="snackColor"
    >
      {{ snackText }}

      <template v-slot:action="{ attrs }">
        <v-btn
          v-bind="attrs"
          text
          @click="snack = false"
        >
          Close
        </v-btn>
      </template>
    </v-snackbar>
  </div>
</template>
<script>

// eslint-disable
export default {
  data: () => ({
    filters: {
      "server": [],
      "PA_PARENT": [],
    },
    search: '',
    initData: [],
    initHeaders: {},
    filteredData: [],
    snack: false,
    snackColor: '',
    snackText: '',
  }),

  props:["inputData", "inputProps", "waitResponse"],

  computed: {
    isReadOnly () {
      return this.$store.state.selectedTemplate.readonly
    },
    bLoading () {
      if (this.inputData.length > 0) {return false;}
      else {
        if (this.waitResponse) {return true}
        else {return false;}
      }
    },
    isDisabled() {
      return (this.$store.state.selectedTemplate.name === undefined || this.$store.state.selectedTemplate.readonly || this.$store.state.trial) ? true : false
    },
    isDisabledText() {
      var text = "";
      if(this.$store.state.selectedTemplate.name === undefined){text = "no profile selected"}
      if (this.$store.state.selectedTemplate.readonly){text = "READONLY"}
      if (this.$store.state.trial) {text = "TRIAL"}
      return text 
    },

    showData: {
      get () {
        if (this.filters.server.length > 0 || this.filters.PA_PARENT.length > 0){return this.filteredData}
        else {return this.inputData}
      },
    },
    selectedParents: {
      get () {
        return this.filters.PA_PARENT
      }, 
      set (value){
        this.filters.PA_PARENT = value
        this.filterData()
      }
    },
    /* REFRESH TODO
    inputDataComp: {
      get () {
        return this.inputData
      }, 
      set (){
        this.filters.server = []
        this.filters.PA_PARENT = []
        // Emitting refresh event
      }
    },
    */
    selectedServers: {
      get () {
        return this.filters.server
      }, 
      set (value){
        this.filters.server = value
        this.filterData()
      }
    },
  },

  watch: {
    //
  },

  created () {
    //
  },
  
  mounted() {
    this.initHeaders = this.inputProps.headers
    this.filteredData = this.inputData
  },

  methods: {
    filterData(){
      const locFilter = this.filters
      const locData = this.inputData;
      const filterKeys = Object.keys(locFilter);
      this.filteredData = locData.filter(function (eachObj) {
            return filterKeys.every(function (eachKey) {
              if (!locFilter[eachKey].length) {
                return true
              }
              return locFilter[eachKey].includes(eachObj[eachKey]);
           });
       });
    },

    saveEdit (item) {
      if (!this.isDisabled){

        const parent = item.PA_PARENT
        const server = item.server

        const data = {
          "parameterAlias": item.PA_NAME,
          "parameterValue": item.PA_VALUE,
          "type": this.inputProps.typeName,
          ...parent&& {"componentAlias" : parent},
          ...server&& {"server": server},
        }
        console.log("dataobj", data);
          this.snack = true
          this.snackColor = 'success'
          this.snackText = 'Parameter saved'
          this.$emit("data-operation", data);
      } else {
        this.snack = true
        this.snackColor = 'error'
        if (this.$store.state.trial){this.snackText = 'This is a trial version. You can change paramter in full version'}
        else {this.snackText = 'readonly'}
      }
    },
    cancelEdit () {
        this.snack = true
        this.snackColor = 'error'
        this.snackText = 'Canceled'
    },
    openEdit () {
      if (this.isDisabled){
        this.snack = true
        this.snackColor = 'error'
        if (this.$store.state.trial){this.snackText = 'This is a trial version. You can change paramter in full version'}
        else {this.snackText = 'readonly'}
      }
    },
    closeEdit () {
      //
    },
  },
}
</script>
<style>

.v-small-dialog__actions {
  display: None;
}


</style>