<template>
  <v-container fluid>
    <v-row>
      <v-btn 
        class ="flex xs12 mx-16 mt-10 white--text"
        :color= "`${this.$store.state.selectedTemplate.color ? this.$store.state.selectedTemplate.color + ' darken-1': 'primary'}`"
        :disabled="isDisabled"
        @click="createSnapshot"
      >
        <v-badge
          class = "px-2"
          :value="isDisabledBadge"
          content="TRIAL"
          bordered
          color="warning"
        > 
          Create snapshot - {{this.$store.state.selectedTemplate.name}}
        </v-badge>
      </v-btn>

      <v-snackbar
        v-model="snack"
        :timeout="3000"
        :color="snackColor"
      >
        {{ snackText }}

        <template v-slot:action="{ attrs }">
          <v-btn
            v-bind="attrs"
            text
            @click="snack = false"
          >
            Close
          </v-btn>
        </template>
      </v-snackbar>


  <v-col cols="12" sm="12" md="12">
    <ServConfTable
      class ="mx-4 my-10"
      :inputData='filteredEntParams'
      :inputProps ='tableProps.entParams'
      :waitResponse ='waitResponse.entParams'
      @data-operation = 'dataOperation'
    />

    <ServConfTable
      class ="mx-4 my-10"
      :inputData='filteredEntNSSParams'
      :inputProps='tableProps.entNSSParams'
      :waitResponse ='waitResponse.entNSSParams'
      @data-operation = 'dataOperation'
    />

    <ServConfTable
      class ="mx-4 my-10"
      :inputData='filteredEntCDParams'
      :inputProps ='tableProps.entCDParams'
      :waitResponse ='waitResponse.entCDParams'
      @data-operation = 'dataOperation'
    />

    <ServConfTable
      class ="mx-4 my-10"
      :inputData='filteredSerParams'
      :inputProps='tableProps.serParams'
      :waitResponse ='waitResponse.serParams'
      @data-operation = 'dataOperation'
    />

    <ServConfTable
      class ="mx-4 my-10"
      :inputData='filteredSerCDParams'
      :inputProps='tableProps.serCDParams'
      :waitResponse ='waitResponse.serCDParams'
      @data-operation = 'dataOperation'
    />

  </v-col>
  </v-row>
  </v-container>
</template>

<script>
/* eslint-disable */
  import ServConfTable from "../components/ServConfLiveTable";
  
  import CommandsDataService from "../services/CommandsDataService";
  import {
    entLiveParams,
    entLiveCDParams,
    entLiveNSSParams,
    serLiveParams,
    serLiveCDParams,
    snapshot,
  } from "../services/ZookeeperLiveDataService";


  const entParamsDataService = new entLiveParams();
  const entCDParamsDataService = new entLiveCDParams();
  const entNSSParamsDataService = new entLiveNSSParams();
  const serParamsDataService = new serLiveParams();
  const serCDParamsDataService = new serLiveCDParams();
  const ZKsnapshot = new snapshot();
  

  export default {
    name: 'serverConfig',
    components: {
      ServConfTable,
    },
    data: () => ({
      snack: false,
      snackColor: "warning",
      snackText: "",
      waitResponse: {
        'entParams': false,
        'entNSSParams': false,
        'entCDParams': false,
        'serParams': false,
        'serCDParams': false,
      },
      selectedData: {
        'entParams': [],
        'entCDParams': [],
        'entNSSParams': [],
        'serParams': [],
        'serCDParams': [],
      },
      // base 
      entParams: [],
      entCDParams: [],
      entNSSParams: [],
      serParams: [],
      serCDParams: [],

      // filtered
      filteredEntParams: [],
      filteredEntCDParams: [],
      filteredEntNSSParams: [],
      filteredSerParams: [],
      filteredSerCompDefs: [],
      filteredSerCDParams: [],


      tableProps: {
        'entParams': {
          formName: "Enterprise parameters",
          typeName: "entParam",
          isParent: false,
          isServer: false,
          headers: [
            { text: 'Parameter', align: 'start', value: 'PA_NAME', width: "50%", selected: true,},
            { text: 'Value', value: 'PA_VALUE',width: "50%", selected: true,},
          ],
          parents: [],
          servers: [],
        },
        'entCDParams': {
          formName: "Component definitions",
          typeName: "entComp",
          isParent: true,
          isServer: false,
          headers: [
            { text: 'Component', value: 'PA_PARENT' , width: "33%", selected: true,},
            { text: 'Parameter', align: 'start', value: 'PA_NAME', width: "33%", selected: true,},
            { text: 'Value', value: 'PA_VALUE' , width: "33%", selected: true,},
          ],
          parents: [],
          servers: [],
        },
        'entNSSParams': {
          formName: "Named subsystems",
          typeName: "nss",
          isParent: true,
          isServer: false,
          headers: [
            { text: 'Named subsystem', value: 'PA_PARENT' , width: "33%", selected: true,},
            { text: 'Parameter', align: 'start', value: 'PA_NAME', width: "33%", selected: true,},
            { text: 'Value', value: 'PA_VALUE' , width: "33%", selected: true,},
          ],
          parents: [],
          servers: [],
        },
        'serParams': {
          formName: "Server parameters",
          typeName: "serParam",
          isServer: true,
          isParent: false,
          headers: [
            { text: 'Server', value: 'server' , width: "33%", selected: true,},
            { text: 'Parameter', align: 'start', width: "33%", value: 'PA_NAME', selected: true,},
            { text: 'Value', value: 'PA_VALUE' , width: "33%", selected: true,},
          ],
          parents: [],
          servers: [],
        },
        'serCDParams': {
          formName: "Components",
          typeName: "serComp",
          isServer: true,
          isParent: true,
          headers: [
            { text: 'Server', value: 'server' , width: "10%", selected: true,},  
            { text: 'Component', value: 'PA_PARENT' , width: "23%", selected: true,},
            { text: 'Parameter', align: 'start', value: 'PA_NAME',  width: "33%", selected: true,},
            { text: 'Value', value: 'PA_VALUE' ,  width: "33%", selected: true,},
          ],
          parents: [],
          servers: [],
        },
      }
    }),

    computed: {
      isSelectedProfile () {
        if (!this.$store.state.selectedTemplate.name){return false}
        else {return this.$store.state.selectedTemplate.name}
      },
      isChangeProfile () {
        if (!this.$store.state.selectedTemplate.name){return false}
        else {return this.$store.state.selectedTemplate.name}
      },
      isDisabled() {
        return (this.$store.state.selectedTemplate.name === undefined || this.$store.state.trial) ? true : false
      },
      isDisabledBadge() {
        return (this.$store.state.trial) ? true : false
      },
    },

    watch: {
      isSelectedProfile (){
        this.refresh();
      }
    },

    created () {
      //
    },
    
    mounted() {
      this.refresh ();
    },

    methods: {
      refresh() {
        if(this.isSelectedProfile){
          this.getentParams(this.$store.state.selectedTemplate.name);
          this.getentCDParams(this.$store.state.selectedTemplate.name);
          this.getentNSSParams(this.$store.state.selectedTemplate.name);
          this.getserParams(this.$store.state.selectedTemplate.name);
          this.getserCDParams(this.$store.state.selectedTemplate.name);

          this.waitResponse = {
            'entParams': true,
            'entNSSParams': true,
            'entCDParams': true,
            'serParams': true,
            'serCDParams': true,
          }
        }
      },
      async createSnapshot () {
        const response = await ZKsnapshot.createSnapshot(this.$store.state.selectedTemplate.name);
        console.log(response.data)
        this.snack=true
        if (response.data == "Snapshot saved!") {this.snackColor = "success"}
        else {this.snackColor = "error"}
        this.snackText=response.data
      },
      dataTranform (input, table) {
        var result = [];
        this.tableProps[table].parents = [];
        for (let parent in input) {
          for (let param in input[parent]) {
            result.push({
              "PA_PARENT" : parent,
              "PA_NAME": param,
              "PA_VALUE": input[parent][param]
            });
          }
          this.tableProps[table].parents.push(parent);
        }
        return result
      },

      dataTranformEntParam (input) {
        var result = [];
        for (let param in input) {
          result.push({
            "PA_NAME": param,
            "PA_VALUE": input[param]
          });
        }
        return result
      },

      dataTranformSerParam (input, table) {
        this.tableProps[table].servers = [];
        var result = [];
        for (let server in input) {
          for (let param in input[server]) {
            result.push({
              "server" : server,
              "PA_NAME": param,
              "PA_VALUE": input[server][param]
            });
          }
          this.tableProps[table].servers.push(server);
        }
        return result
      },

      dataTranformSerCD (input, table) {
        var result = [];
        this.tableProps[table].parents = [];
        this.tableProps[table].servers = [];
        for (let server in input){
          for (let parent in input[server]) {
            for (let param in input[server][parent]) {
              result.push({
                "server": server,
                "PA_PARENT" : parent,
                "PA_NAME": param,
                "PA_VALUE": input[server][parent][param]
              });
            }
            this.tableProps[table].parents.push(parent);
            this.tableProps[table].servers.push(server);
          }
        }
        return result
      },

      async dataOperation(data){
        try {
          const response = await CommandsDataService.changeParameter(this.$store.getters.selectedTemplate.name, data);
          console.log(response)
        } catch(e){
          console.log(e)
        }
        
      },

      async getentParams(query) {
        try {
          const response            = await entParamsDataService.getAll(query);
          this.entParams            = this.dataTranformEntParam(response.data, "entParams")
          this.filteredEntParams    = this.dataTranformEntParam(response.data, "entParams")
          this.waitResponse.entParams = false
        } catch(e) {
          this.waitResponse.entParams = false
          console.log(e);
        }
      },
      async getentCDParams(query) {
        try {
          const response                = await entCDParamsDataService.getAll(query);
          this.entCDParams              = this.dataTranform(response.data, "entCDParams")
          this.filteredEntCDParams      = this.dataTranform(response.data, "entCDParams")
          this.waitResponse.entCDParams = false
        } catch(e) {
          this.waitResponse.entCDParams = false
          console.log(e);
        }
      },
      async getentNSSParams(query) {
        try {
          const response                = await entNSSParamsDataService.getAll(query);
          this.entNSSParams             = this.dataTranform(response.data, "entNSSParams")
          this.filteredEntNSSParams     = this.dataTranform(response.data, "entNSSParams")
          this.waitResponse.entNSSParams = false
        } catch(e) {
          this.waitResponse.entNSSParams = false
          console.log(e);
        }
      },
      async getserParams(query) {
        try {
          const response            = await serParamsDataService.getAll(query);
          this.serParams            = this.dataTranformSerParam(response.data, "serParams")
          this.filteredSerParams    = this.dataTranformSerParam(response.data, "serParams")
          this.waitResponse.serParams = false
        } catch(e) {
          this.waitResponse.serParams = false
          console.log(e);
        }
      },
      async getserCDParams(query) {
        try {
          const response            = await serCDParamsDataService.getAll(query);
          this.serCDParams          = this.dataTranformSerCD(response.data, "serCDParams");
          this.filteredSerCDParams  = this.dataTranformSerCD(response.data, "serCDParams");
          this.waitResponse.serCDParams = false;
        } catch(e) {
          this.waitResponse.serCDParams = false;
          console.log(e);
        }
      }, 
  }
}
</script>
<style scoped>
  .v-btn { min-height: 58px; }
</style>